/**
 * @returns {boolean}
 */
export const isMobile = () => {
  return navigator?.userAgentData?.mobile || false;
};

/**
 * @param {string} key
 * @param {boolean} value
 * @returns {null|boolean}
 */
export const setLocalStorageBoolean = (key = '', value) => {
  localStorage.setItem(key, String(Number(value)));
};

/**
 * @param {string} key
 * @param {*} defaultValue
 * @returns {null|boolean}
 */
export const getLocalStorageBoolean = (key = '', defaultValue = null) => {
  const item = localStorage.getItem(key);

  return item === null ? defaultValue : Boolean(Number(item));
};
