<template>
  <div class="question">
    <div class="question__number">Question #{{ question.number }}</div>
    <case-study v-if="question.isCaseStudy" class="question__case-study" />
    <div class="question__text" v-html="questionHTML"></div>
    <ul class="question__answers">
      <TheAnswer
        v-for="(answer, index) in question.answers"
        :key="index"
        :index="index"
        :answer-text="answer"
        :question="question"
        :show-answer="showAnswer"
        :highlight-possible-answers="highlightPossibleAnswers"
        :show-all-answers="showAllAnswers"
        :no-community-vote="question.noCommunityVote"
        class="question__answer"
      />
    </ul>
    <div v-if="!showAllAnswers" class="question__buttons">
      <button
        type="button"
        class="question__button question__button_show-answer-button"
        @click="showAnswer = !showAnswer"
      >
        {{ showAnswer ? 'Hide' : 'Show' }} answer
      </button>
    </div>
    <div
      v-if="(showAllAnswers || showAnswer) && question.noCommunityVote"
      class="question__no-community-vote"
    >
      There is no community discussion on this question.
    </div>
    <div v-if="showMultipleLetters" class="question__multiple-answers">
      <div>
        Community: {{ getLettersFromIndexes(question.communityAnswer) }}
      </div>
      <div v-if="highlightPossibleAnswers">
        Original: {{ getLettersFromIndexes(question.possibleAnswer) }}
      </div>
    </div>
    <div v-if="question.screenshot" class="question__screenshot">
      <button
        class="screenshot__button"
        type="button"
        @click="showScreenshot = !showScreenshot"
      >
        {{ showScreenshot ? 'Hide' : 'Show' }} screenshot
      </button>
      <div v-if="showScreenshot">
        <img
          :src="questionScreenshot"
          alt="screenshot"
          class="question__screenshot"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { LETTERS } from '@/constants.js';
import TheAnswer from '@/components/TheAnswer.vue';
import CaseStudy from '@/components/CaseStudy.vue';

export default {
  name: 'TheQuestion',

  components: {
    TheAnswer,
    CaseStudy,
  },

  props: {
    question: {
      type: Object,
      required: true,
    },
    highlightPossibleAnswers: {
      type: Boolean,
      required: true,
    },
    showAllAnswers: {
      type: Boolean,
      required: true,
    },
  },

  data() {
    return {
      showAnswer: false,
      showScreenshot: false,
    };
  },

  computed: {
    /**
     * @returns {string}
     */
    questionHTML() {
      return this.question.text.replaceAll(
        /\[IMG=q(\d+)_img(\d+)\.(\w+)]/g,
        (match) => {
          const fileName = match.slice(5, -1);
          const imageSrc = require(`@/assets/images/quiz/${fileName}`);

          return `<img src="${imageSrc}" class="question__image" alt="" />`;
        }
      );
    },

    /**
     * @returns {boolean}
     */
    showMultipleLetters() {
      return (
        !this.question.noCommunityVote &&
        (this.showAllAnswers || this.showAnswer) &&
        this.highlightPossibleAnswers &&
        Array.isArray(this.question.communityAnswer)
      );
    },

    /**
     * @returns {string}
     */
    questionScreenshot() {
      return this.question.screenshot
        ? require(`@/assets/images/quiz/${this.question.screenshot}`)
        : '';
    },
  },

  methods: {
    /**
     * @param {number[]} indexes
     * @returns {string}
     */
    getLettersFromIndexes(indexes = []) {
      if (!Array.isArray(indexes)) {
        return LETTERS[indexes];
      }

      return indexes.map((index) => LETTERS[index]).join(', ');
    },
  },
};
</script>

<style lang="scss" scoped>
.question {
  .question__number {
    display: block;
    padding: 8px;
    margin-bottom: 8px;
    background-color: #2f2f2f;
    color: #ddd;
  }

  .question__text,
  .question__answer,
  .question__no-community-vote,
  .question__multiple-answers,
  .question__screenshot {
    margin: 16px 0;
  }

  .question__text {
    padding: 0 8px;
  }

  .question__show-screenshot-button {
    margin-bottom: 8px;
  }

  .question__no-community-vote {
    color: orangered;
    font-size: 14px;
  }

  .screenshot__button {
    margin: 0;
    padding: 0;
    background-color: transparent;
    color: #ccc;
    border: 0;

    &:hover {
      text-decoration: underline;
    }
  }

  .question__buttons {
    display: flex;
    align-items: center;
    justify-content: center;

    .question__button {
      margin-right: 8px;
      padding: 8px;
      background-color: #444;
      color: #ccc;
      border: 0;
    }
  }
}
</style>
