import { createRouter, createWebHashHistory } from 'vue-router';
import TheQuiz from '@/components/TheQuiz.vue';

const routes = [
  {
    path: '/',
    redirect: '/quiz/1',
  },
  {
    path: '/quiz/:page?',
    name: 'quiz',
    component: TheQuiz,
    props: true,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 };
  },
});

export default router;
