<template>
  <li
    v-html="answerHTML"
    class="answer"
    :class="{
      answer_possible: highlightPossibleAnswer,
      answer_correct: highlightCorrectAnswer,
    }"
  />
</template>

<script>
import { LETTERS } from '@/constants.js';

export default {
  name: 'TheAnswer',

  props: {
    index: {
      type: Number,
      required: true,
    },
    answerText: {
      type: String,
      required: true,
    },
    question: {
      type: Object,
      required: true,
    },
    showAnswer: {
      type: Boolean,
      required: true,
    },
    highlightPossibleAnswers: {
      type: Boolean,
      required: true,
    },
    showAllAnswers: {
      type: Boolean,
      required: true,
    },
    noCommunityVote: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    /**
     * @returns {string}
     */
    answerHTML() {
      return `${LETTERS[this.index]}. ${this.answerText}`.replaceAll(
        /\[IMG=q(\d+)_img(\d+)\.(\w+)]/g,
        (match) => {
          const fileName = match.slice(5, -1);
          const imageSrc = require(`@/assets/images/quiz/${fileName}`);

          return `<img src="${imageSrc}" class="answer__image" alt="" />`;
        }
      );
    },

    /**
     * @returns {boolean}
     */
    isCommunityAnswer() {
      if (Array.isArray(this.question.communityAnswer)) {
        return this.question.communityAnswer.includes(this.index);
      }

      return this.index === this.question.communityAnswer;
    },

    /**
     * @returns {boolean}
     */
    isPossibleAnswer() {
      if (Array.isArray(this.question.possibleAnswer)) {
        return this.question.possibleAnswer.includes(this.index);
      }

      return this.index === this.question.possibleAnswer;
    },

    /**
     * @returns {boolean}
     */
    highlightPossibleAnswer() {
      return (
        this.isPossibleAnswer &&
        (this.showAllAnswers || this.showAnswer) &&
        (this.highlightPossibleAnswers || this.noCommunityVote)
      );
    },

    /**
     * @returns {boolean}
     */
    highlightCorrectAnswer() {
      return (
        !this.noCommunityVote &&
        this.isCommunityAnswer &&
        (this.showAllAnswers || this.showAnswer)
      );
    },
  },
};
</script>

<style lang="scss" scoped>
.answer {
  padding: 2px 6px;
  border: 2px solid transparent;
  border-radius: 6px;

  &.answer_possible {
    border-color: #9fa728;
  }

  &.answer_correct {
    border-color: #28a745;
  }
}
</style>
