<template>
  <div class="quiz" :class="{ quiz_mobile: isMobile }">
    <h1 class="quiz__title">
      Google Cloud Developer Certification Quiz (community edition)
    </h1>
    <div class="quiz__menu">
      <label class="quiz__menu-checkbox">
        <input type="radio" v-model="showAllAnswers" :value="true" />
        Learn
      </label>
      <label class="quiz__menu-checkbox">
        <input type="radio" v-model="showAllAnswers" :value="false" />
        Test
      </label>
      <label class="quiz__menu-checkbox">
        <input type="checkbox" v-model="highlightPossibleAnswers" />
        Highlight original answers (probably incorrect)
      </label>
    </div>
    <TheQuestion
      v-for="question in questionsToDisplay"
      :key="question.number"
      :question="question"
      :highlight-possible-answers="highlightPossibleAnswers"
      :show-all-answers="showAllAnswers"
      class="quiz__question"
    />
    <div class="quiz__pagination">
      <ThePagination
        v-model="selectedPage"
        :records="questions.length"
        :per-page="perPage"
        :options="{
          chunk: isMobile ? 6 : 10,
          edgeNavigation: !isMobile,
        }"
        @paginate="onPaginate"
      />
    </div>
  </div>
</template>

<script>
import ThePagination from 'v-pagination-3';
import TheQuestion from '@/components/TheQuestion.vue';
import questions from '@/data/questions.js';
import {
  isMobile,
  getLocalStorageBoolean,
  setLocalStorageBoolean,
} from '@/utils/helpers.js';

const LS_SHOW_ALL_ANSWERS = 'showAllAnswers';
const LS_HIGHLIGHT_POSSIBLE_ANSWERS = 'highlightPossibleAnswers';

export default {
  name: 'TheQuiz',

  props: {
    page: {
      type: String,
      default: '1',
    },
  },

  components: {
    ThePagination,
    TheQuestion,
  },

  data() {
    return {
      selectedPage: 1,
      perPage: 5,
      questions,
      showAllAnswers: getLocalStorageBoolean(LS_SHOW_ALL_ANSWERS, true),
      highlightPossibleAnswers: getLocalStorageBoolean(
        LS_HIGHLIGHT_POSSIBLE_ANSWERS,
        false
      ),
    };
  },

  computed: {
    /**
     * @returns {boolean}
     */
    isMobile() {
      return isMobile();
    },

    /**
     * @returns {number}
     */
    displayFrom() {
      return (this.selectedPage - 1) * this.perPage;
    },
    /**
     * @returns {array}
     */
    questionsToDisplay() {
      return this.questions.slice(
        this.displayFrom,
        this.displayFrom + this.perPage
      );
    },
  },

  watch: {
    page: {
      immediate: true,
      handler(value) {
        const number = Number(value);

        if (isNaN(number) || number <= 0) {
          this.$router.push({ name: 'quiz', params: { page: 1 } });
        } else {
          this.selectedPage = number;
        }
      },
    },

    showAllAnswers(value) {
      setLocalStorageBoolean(LS_SHOW_ALL_ANSWERS, value);
    },

    highlightPossibleAnswers(value) {
      setLocalStorageBoolean(LS_HIGHLIGHT_POSSIBLE_ANSWERS, value);
    },
  },

  methods: {
    /**
     * @param {number} page
     */
    onPaginate(page = 1) {
      this.$router.push({ name: 'quiz', params: { page } });
    },
  },
};
</script>

